import React from 'react';
import ApexChartGauge from '../components/charts/sc-apexChart-gauge';
import ApexChartLine from '../components/charts/sc-apexChart-line';

import Card, { CardBody } from '../components/sc-card';
import Progress from '../components/sc-progress';
import Nav from '../components/sc-nav';
import {error} from './sc-error';

import requestSensor from '../assets/js/utils/AxiosRequest';

function Dashboard() {
    return (
        <div className="container-fluid mt-2 pt-2">
            {/* <!-- Content Row --> */}
            <div className="d-flex flex-column mb-3">

                <div className="d-flex align-items-center justify-content-between mb-3">
                    <div className="d-none d-sm-inline h3 text-dark my-auto">Dashboard</div>
                    <div className="d-none d-sm-inline">
                        <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button" onClick={requestSensor}>
                            <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                            <span className="text px-4">Generate Report</span>
                        </button>
                    </div>
                </div>

                <div className="row align-items-top mb-2">
                    <div className="d-none d-sm-block col-md-12 col-xl-4">
                        <div className="card shadow rounded-0 mb-3">
                            <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                                Air Conditioner
                            </small>
                        
                            <div className="card-body pb-1">
                                <div className="col px-2">
                                    <CardBody header="Evaporator Coil" value={12.76} />
                                    <div className="d-block dropdown-divider mb-4"></div>

                                    <Progress header="Relative Humidity" value={43.12} />
                                    <CardBody header="Dewpoint Temperature" value={10.23}
                                        badge={false} />
                                </div>
                            </div>
                        </div>

                        {/* <small className="d-block float-right text-dark">
                            <strong className="text-midnight">Last Updated:</strong> 09:27:42 PM
                        </small> */}
                    </div>

                    <div className="d-none d-sm-block col-md-12 col-xl-8">
                        <div className="card shadow rounded-0 mb-3">
                            <small className="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                                Rack Cooling Index (RCI)
                            </small>

                            <div className="card-body pb-3">
                                <div className="row px-2">
                                    <div className="col-md-12 col-xl-6">
                                        <CardBody header="Supply Temperature" value={22.31} /></div>
                                    <div className="col-md-12 col-xl-6">
                                        <CardBody header="Return Temperature" value={29.98} /></div>
                                </div>

                                <div className="d-block dropdown-divider my-auto"></div>

                                <div className="row px-2">
                                    <div className="col-md-12 col-xl-6 mb-2">
                                        <ApexChartGauge chartLabel="RCI-Lo" chartHeight={320}
                                            value={36.53}/>
                                    </div>
                                    <div className="col-md-12 col-xl-6 mb-2">
                                        <ApexChartGauge chartLabel="RCI-Hi" chartHeight={320}
                                            value={72.45}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="d-none d-sm-block card shadow">
                    <div className="card-body pb-0">
                        <ApexChartLine chartLabel="Line Chart" chartHeight={320}/>
                    </div>
                </div>
            </div>

            {/* <!-- Datatable Tabs --> */}
            <div className="d-none d-md-block mb-3">
                <Nav />
            </div>
            
            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">
                {error ('Mobile view is not supported yet :(')}
            </div>
        </div>
    );
};

export default Dashboard;