/* Main.Js */

import React, { useState } from 'react';
import { Switch } from "react-router-dom";
import { Private } from './assets/js/utils/AccessRoute';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/custom.css';
import './assets/css/navigation.css';

import Sidebar from './components/sc-sidebar-alt';
import Topbar from './components/sc-topbar-alt';
import Footer from './components/sc-footer';

import Dashboard from './views/sc-dashboard';
import Error from './views/sc-error';
 
function Main (props) {
    const [toggle, setToggle] = useState(true);

    return (
        
        // <div id="wrapper">
        //     <Sidebar />
        //     <div id="content-wrapper" className="d-flex flex-column">
        //         <div id="content">
        //             <Topbar {...props} />
        //             <Switch>
        //                 <Private exact path="/" component={Dashboard} />
        //                 <Private exact path="/help" component={Error} />
        //             </Switch>
        //         </div>
        //         <Footer />
        //     </div>
        // </div>

        <div id="content-wrapper" className="d-flex flex-column">
            <div className={toggle ? "sb-nav-fixed" : "sb-sidenav-toggled"} id="content">
                <Topbar state={[toggle, setToggle]} {...props} />
                <div id="layoutSidenav">
                    <Sidebar/>
                    <div id="layoutSidenav_content">
                        <Switch>
                            <Private exact path="/" component={Dashboard} />
                            <Private exact path="/help" component={Error} />
                        </Switch>
                        <Footer />
                    </div>
                </div>
            </div> 
        </div>
    );
};

export default Main;
